.mainlist_container{
    width: 100%;
    height: 100%;
    background-color: #141414;
     color: white;
}
.mainlist_container h2{
    margin-left: 40px;
}
.mainlist_box{
    width: 95%;
    height: 100%;
    margin: auto;
    margin-bottom: 50px;
    
}

.mainlist_list{
    color: white;
    width: 100%;
    height: 100%;
    font-size: 16px;
}

.mainlist_list img{
    cursor: pointer;
    margin: auto;
    width: 250px;
    height: 350px;
    transition: all ease-in 0.2s;
}
.poster_rank{
  font-size: 25px;
  height: 100%;
}
.poster_title{
font-size: 25px;
text-align: center;
   
}
.mainlist_list img:hover{
    scale: 1.1;
}



