.searchcontainer{
    padding-top: 8vh;
    display: flex;
  flex-direction: column;
  align-items:center;
    
}
.searchwrapper{

    display: flex;
    width: 80%;
    height: 15vh;
    margin-bottom: 2vh;
    color: white;
    border: 1px solid white;
    margin-left: auto;
    margin-right: 30px;
    overflow: hidden;
}
.searchposter{
    width: 10%;
    height: 100%;
     
}
.searchposter img{
    width: 100%;
    height: 100%;
}
.detail{
 width: 90%;
    box-sizing: border-box;
    flex-wrap: wrap;
    align-items: center;
}
.detailtitle{
    width: 100%;
     padding: 10px 15px;
   
}
.detailoverview{
     padding: 10px 15px;
}
.detailoverview p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: 10px;
}