.navbar{
  width: 100%;
  position: fixed;
    display: flex;
    align-items: center;
    height: 8vh;
      white-space: nowrap;
    background: linear-gradient(to bottom, rgba(0,0,0,0.8), rgba(0,0,0,0));
    transition: background-color 0.5s ease;
    z-index: 1000;
}
.navbar.scroll{
  
  top: 0;
  left: 0;
  width: 100%;
  background-color: #0C0C0C; /* 스크롤이 올라올 때 변경할 색상 */
  
}

.linklogo{
    text-decoration: none;
    font-size: 24px;
    font-weight: bold;
    color: #E50914;
}
.linklogoimg{
 margin-left: 30px;
 font-size: 2em;
 margin-right: 15px;
 color: #f5010c;
 
}

.navbar_menu{
  display: flex;
  justify-content: flex-start; /* 왼쪽 정렬 추가 */
  width: auto; /* 부모 요소 가로폭 차지하도록 변경 */
  margin-left: 30px;
  padding: 0; /* 기본 padding 제거 */
}
.navbar_menu li{
    list-style-type: none;
}
.linkmenu{
    font-size: 20px;
    margin-right: 30px;
    text-decoration: none;
    color: white;
}
.navbar_search{

    font-size: 20px;
    color: white;
    margin-left: auto;
    margin-right: 50px;


}
 .navbar_search.active{
    height: 40px;
    background-color: #0C0C0C;
    border: 1px solid white;
    opacity: 0.8;
 } 
 .searchBar_input{

   background-color: #0C0C0C;
   width: 200px; 
   height: 100%;
   border: none;
   outline: none;
   color: white;
   opacity: 0.8;
 
 }
 .searchBar_icon{
    margin-left: 20px;
    margin-right: 10px;
 }
 @media (max-width: 768px) {
.navbar_search {
    font-size: 16px; /* 모바일 화면에서 검색 바 폰트 크기를 조절합니다. */
    margin-right: 20px; /* 검색 바의 오른쪽 여백을 줄입니다. */
  }

  .navbar_search.active {
    height: 30px; /* 활성화된 검색 바의 높이를 줄입니다. */
  }

  .searchBar_input {
    width: 150px; /* 검색 입력 창의 너비를 줄입니다. */
    font-size: 14px; /* 검색 입력 창의 폰트 크기를 조절합니다. */
  }

  .linklogo {
    font-size: 15px; /* 로고의 폰트 크기를 조절합니다. */
  }

  .linklogoimg {
    font-size: 0.8em; /* 로고 아이콘의 크기를 줄입니다. */
    margin-right: 10px; /* 로고 아이콘의 오른쪽 여백을 줄입니다. */
  }

  .linkmenu {
    font-size: 12px; /* 네비게이션 메뉴의 폰트 크기를 조절합니다. */
    margin-right: 20px; /* 각 메뉴 사이의 간격을 조절합니다. */
  }
}